import Dialog from '../../wxcomponents/vant/dist/dialog/dialog';
var app = getApp().globalData
export default {
	components: {
	},
	data() {
		return {
			showActive:false,
			fin:false,
			loadingOne:false,
			loadingTwo:false,
			n:0,  //控制三页分类选项卡的
			n1:0,
			n2:0,
			currentIndex:1, //控制头部选项卡的
			nav_list: ['友好物', '巨划算', '我关注'],
			bannerList:[],
			swiperCurrent: 0,
			classifyList: [], //分类列表，自己加全部
			ListData:[], //友好物列表
			category_id:'',//首页分类id
			page:1, //首页分页page
			category_id1:'',//聚划算分类id
			page1:1, //聚划算分页page
			chinaList:[],
			category_id2:'',//我关注分类id
			page2:1, //我关注分页page
			voteList:[],// 我关注的列表
			everyList:[], // 大家都在看列表
			scrollTop0:0,
			scrollTop1:0,
			scrollTop2:0,
			topShow:true,
			leftItems:[],
			rightItems:[],
			leftHeight:0,
			rightHeight:0,
			// 新增分类
			pagelist:["最新上传","关注量","查看量"],
			pageIndex1:0,
			pageIndex2:0,
			pageIndex3:0,
			order_type1:1,
			order_type2:1,
			order_type3:1,
			youList:[],
			city:'',
			allow0:false,
			showTitle:false
			
		}
	},
	onPullDownRefresh() {
		this.page1 = 1
		this.getChina()
		setTimeout(function () {
		        uni.stopPullDownRefresh();
				uni.showToast({
				    title: '刷新成功',
				    duration: 1000,
					icon:'none'
				});
		  }, 1000);
	
	},
	// 上拉加载更多
	onReachBottom(){
		
		if(this.allow0){
			this.allow0 = false
			this.loadingTwo = true
			this.page1++
			this.getChina()
		}
		
		
	},
	onLoad() {
		this.getBanner()
		this.getClassify()
		this.getChina()
		this.getYou()
	},
	onShareAppMessage(res) {
	    return {
	      title: '德申汇小申展',
	      imageUrl:app.shareImg,
	      path: '/pagesTwo/SmallStretchNew/SmallStretchNew'
	    }
	},
	onPageScroll(e) {
		if(e.scrollTop>200){
			this.topShow = false
		}else{
			this.topShow = true
		}
		
	},
	methods: {
		youhaowu(){
			uni.navigateTo({
				url:'../../pagesTwo/SmallStretchNewItem/SmallStretchNewItem'
			})
		},
		// 大家都在看
		everyLook(){
			// console.log(123)
			if(uni.getStorageSync('setInfo') ===0){
				uni.showToast({
				    title: '请完善个人信息',
				    duration: 2000,
					icon:'none'
				});
				
				setTimeout(function() {
					uni.navigateTo({
						url:'../../pages/PersonalIdcard/PersonalIdcard'
					})
				}, 300);
				
			}else{
				if (uni.getStorageSync('isVip') === 0) {
					Dialog.confirm({
					  title: '提示',
					  message: '您还不是VIP用户,没有发布权限',
					  confirmButtonText:'去支付'
					}).then(() => {
						uni.navigateTo({
							url:'../pay/pay'
						})
					}).catch(() => {
					  // on cancel
					});
				  } else if (uni.getStorageSync('isVip') === 1) {
					
					  uni.navigateTo({
					  	url:'../../pagesTwo/SmallStretchNewEvery/SmallStretchNewEvery'
					  })
					
				  } else {
					uni.clearStorageSync()
					uni.showToast({
					    title: '请登录',
					    duration: 2000,
						icon:'none'
					});
					
					uni.reLaunch({
						url: '../../pages/login/login'
					})
				  }
			}
		},
		// 跳转发布历史
		history(){
			// console.log(123)
			if(uni.getStorageSync('setInfo') ===0){
				uni.showToast({
				    title: '请完善个人信息',
				    duration: 2000,
					icon:'none'
				});
				
				setTimeout(function() {
					uni.navigateTo({
						url:'../../pages/PersonalIdcard/PersonalIdcard'
					})
				}, 300);
				
			}else{
				if (uni.getStorageSync('isVip') === 0) {
					Dialog.confirm({
					  title: '提示',
					  message: '您还不是VIP用户,没有发布权限',
					  confirmButtonText:'去支付'
					}).then(() => {
						uni.navigateTo({
							url:'../pay/pay'
						})
					}).catch(() => {
					  // on cancel
					});
				  } else if (uni.getStorageSync('isVip') === 1) {
					
					  uni.navigateTo({
					  	url:'../../pagesMy/IssueHistory/IssueHistory'
					  })
					
				  } else {
					uni.clearStorageSync()
					uni.showToast({
					    title: '请登录',
					    duration: 2000,
						icon:'none'
					});
					
					uni.reLaunch({
						url: '../../pages/login/login'
					})
				  }
			}
		},
		// 跳转关注界面
		myVote(){
			// console.log(123)
			if(uni.getStorageSync('setInfo') ===0){
				uni.showToast({
				    title: '请完善个人信息',
				    duration: 2000,
					icon:'none'
				});
				
				setTimeout(function() {
					uni.navigateTo({
						url:'../../pages/PersonalIdcard/PersonalIdcard'
					})
				}, 300);
				
			}else{
				if (uni.getStorageSync('isVip') === 0) {
					Dialog.confirm({
					  title: '提示',
					  message: '您还不是VIP用户,没有发布权限',
					  confirmButtonText:'去支付'
					}).then(() => {
						uni.navigateTo({
							url:'../pay/pay'
						})
					}).catch(() => {
					  // on cancel
					});
				  } else if (uni.getStorageSync('isVip') === 1) {
					
					  uni.navigateTo({
					  	url:'../../pagesTwo/SmallStretchNewVote/SmallStretchNewVote'
					  })
					
				  } else {
					uni.clearStorageSync()
					uni.showToast({
					    title: '请登录',
					    duration: 2000,
						icon:'none'
					});
					
					uni.reLaunch({
						url: '../../pages/login/login'
					})
				  }
			}
		},
		bindPickerChange(e){
			
				this.city = e.detail.value[1].replace(/省|市|自治区|特别行政区|壮族|回族|维吾尔|城区/g, '')
				uni.showToast({
				    title: '切换城市为：' + this.city,
				    mask: "true",
					icon:'none'
				});
				this.$nextTick(()=>{
					this.getChina()
				})
		},
		getYou(){
			this.$api.high_quality_three({
			
			}).then(res => {
				this.youList = res.data.data
			})
		},
		// 聚划算切换关注等
		pageChange1(index){
			this.pageIndex2 = index
			if(index==0){
				this.order_type2 = 1
				uni.showToast({
				    title: '已为您切换至最新上传',
				    mask: "true",
					icon:'none'
				});
				
			}else if(index == 1){
				this.order_type2 = ''
				uni.showToast({
				    title: '已为您切换至关注量',
				    mask: "true",
					icon:'none'
				});
			}else{
				this.order_type2 = 2
				uni.showToast({
				    title: '已为您切换至查看量',
				    mask: "true",
					icon:'none'
				});
			}
			this.showActive = false
			this.getChina()
		},
		search(){
			uni.navigateTo({
				url:'../../pagesMy/Search/Search'
			})
		},
		// 发布
		Issue(){
			// console.log(123)
			if(uni.getStorageSync('setInfo') ===0){
				uni.showToast({
				    title: '请完善个人信息',
				    duration: 2000,
					icon:'none'
				});
				
				setTimeout(function() {
					uni.navigateTo({
						url:'../../pages/PersonalIdcard/PersonalIdcard'
					})
				}, 300);
			}else{
				if (uni.getStorageSync('isVip') === 0) {
					Dialog.confirm({
					  title: '提示',
					  message: '您还不是VIP用户,没有发布权限',
					  confirmButtonText:'去支付'
					}).then(() => {
						uni.navigateTo({
							url:'../pay/pay'
						})
					}).catch(() => {
					  // on cancel
					});
				  } else if (uni.getStorageSync('isVip') === 1) {
					
					  uni.navigateTo({
					  	url:'../../pagesMy/StretchIndexSend/StretchIndexSend'
					  })
					
				  } else {
					uni.clearStorageSync()
					uni.showToast({
					    title: '请登录',
					    duration: 2000,
						icon:'none'
					});
					
					uni.reLaunch({
						url: '../../pages/login/login'
					})
				  }
			}
		},
		// 置顶
		fnscroll(){
			uni.pageScrollTo({
				scrollTop: 0,
				duration: 300
			})
		},
		// 去详情
		goSmallStretchPvw(id){
			uni.navigateTo({
				url:'../../pagesMy/SmallStretchPvw/SmallStretchPvw?id='+id
			})
		},
		// banner跳转
		bannerGo(item){
			// type 0  xiangqing 
			console.log(item)
			if(item.type==0){
				uni.navigateTo({
					url:'../../pagesMy/SmallStretchPvw/SmallStretchPvw?id='+item.var
				})
			}else{
				uni.navigateTo({
					url:'../../pagesMy/view/view?url='+ item.href
				})
			}
		},
		// 首页聚划算数据
		getChina () {
		      this.$api.SmallStretchIndex({
		        token: uni.getStorageSync("token"),
		        user_id: uni.getStorageSync("userId"),
		        type: 0,
		        category_id: this.category_id1,
		        page: this.page1,
				order_type:this.order_type2,
				city:this.city
		      }).then(res => {
		        if (res.data.code === '200') {
					
					// 第一页的时候
					if(this.page1 === 1){
						this.chinaList = res.data.data
						// 大于20条允许 继续加载,少于20条不允许加载并且显示没有更多
						if(res.data.data.length>=20){
							// 控制是否允许加载的变量
							this.allow0 = true
						}else{
							// 控制没有更多的变量
							this.showTitle = true
							this.allow0 = false
						}														
					}else{
						// 第二页的时候
						this.chinaList = this.chinaList.concat(res.data.data)
						if(res.data.data.length>=20){
							this.allow0 = true
						}else{
							this.showTitle = true
							this.allow0 = false
						}	
					}
					// 加载进度条,加载完都得去掉
					this.loadingTwo = false
					
		        }else if (res.data.code === '1010') {
		          uni.clearStorageSync()
		          uni.showToast({
		              title: '登录过期，请重新登录',
		              duration: 2000,
		          	icon:'none'
		          });
		          
		          uni.reLaunch({
		          	url: '../../pages/login/login'
		          })
		        }
		
		      }).catch((res) => {
		        console.log(res)
		      })
		},
		
		// 控制商品分类样式
		classifyChange(ind,id){
			this.n = ind
			this.category_id = id
			this.page = 1
			this.getData()
		},
		classifyChange1(ind,id){
			uni.pageScrollTo({
				scrollTop:0
			})
			this.n1 = ind
			this.category_id1 = id
			this.page1 = 1
			this.getChina()
		},
		classifyChange2(ind,id){
			this.n2 = ind
			this.category_id2 = id
			this.myVote()
		},
		// 商品分类
		getClassify () {
		      this.$api.SmallStretchGoodsCategory({
		      }).then(res => {
		        this.classifyList = res.data.data
		        this.classifyList.unshift(
		          {
		            'id': '',
		            'name': '全部'
		          }
		        )
		      })
		},
	    // 修改轮播图的 分页器样式用的
		changeSwiper(e) {
		    this.swiperCurrent = e.detail.current;
		},
		
        // 获取banner
		getBanner () {
			  this.$api.SmallStretchBanner({
			  }).then(res => {
				  // console.log(res)
				this.bannerList = res.data.data
			  })
	    },
	    
	}
}
